// in src/myondemand.js
import React, { Fragment } from "react";
import {
  List,
  Responsive,
  SimpleList,
  Edit,
  Create,
  Datagrid,
  ReferenceField,
  TextField,
  EditButton,
  DisabledInput,
  Filter,
  ReferenceInput,
  SelectInput,
  SimpleForm,
  TextInput,
  BulkDeleteButton,
  Show,
  SimpleShowLayout,
  RichTextField,
  TabbedForm,
  FormTab
} from "react-admin";
import ResetViewsButton from "./resetviewsbutton";
import RichTextInput from "ra-input-rich-text";

export const PostFilter = props => (
  <Filter {...props}>
    <TextInput label="Cari Permintaan" source="q" alwaysOn />
    <ReferenceInput source="customerId" reference="customers" allowEmpty>
      <SelectInput optionText="name" />
    </ReferenceInput>
  </Filter>
);

const OndBulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Checklist" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);

export const PostList = props => (
  <List
    {...props}
    filters={<PostFilter />}
    bulkActionButtons={<OndBulkActionButtons />}
    title="Permintaan"
  >
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.id}
          secondaryText={record => record.type}
          tertiaryText={record => record.quantity}
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <ReferenceField
            label="Bank Sampah"
            source="customerId"
            reference="customers"
          >
            <TextField source="name" />
          </ReferenceField>
          <ReferenceField label="Tipe" source="pricingId" reference="pricings">
            <TextField source="name" />
          </ReferenceField>
          <TextField label="Jumlah" source="quantity" />
          <ReferenceField
            label="Harga Satuan"
            source="pricingId"
            reference="pricings"
          >
            <TextField source="price" />
          </ReferenceField>
          <RichTextField label="Keterangan" source="comment" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);

const PostTitle = ({ record }) => {
  return <span>Post {record ? `"${record.id}"` : ""}</span>;
};

export const PostEdit = props => (
  <Edit title={<PostTitle />} {...props}>
    <TabbedForm>
      <FormTab label="pengguna">
        <DisabledInput source="id" />
        <ReferenceInput
          label="bank sampah"
          source="customerId"
          reference="customers"
        >
          <SelectInput optionText="name" />
        </ReferenceInput>
      </FormTab>

      <FormTab label="barang">
        <ReferenceInput label="tipe" source="pricingId" reference="pricings">
          <SelectInput optionText="name" />
        </ReferenceInput>
        <TextInput label="jumlah" source="quantity" />
      </FormTab>

      <FormTab label="keterangan">
        <RichTextInput label="keterangan" source="comment" />
      </FormTab>
    </TabbedForm>
  </Edit>
);

export const PostCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <ReferenceInput
        label="bank sampah"
        source="customerId"
        reference="customers"
      >
        <SelectInput optionText="name" />
      </ReferenceInput>
      <ReferenceInput label="tipe" source="pricingId" reference="pricings">
        <SelectInput optionText="name" />
      </ReferenceInput>
      <TextInput label="jumlah" source="quantity" />

      <RichTextInput label="keterangan" source="comment" />
    </SimpleForm>
  </Create>
);

export const PostShow = props => (
  <Show {...props}>
    <SimpleShowLayout>
      <TextField source="id" />
      <ReferenceField
        label="Bank sampah"
        source="customerId"
        reference="customers"
      >
        <TextField source="name" />
      </ReferenceField>
      <ReferenceField label="Tipe" source="pricingId" reference="pricings">
        <TextField source="name" />
      </ReferenceField>
      <TextField source="quantity" />
      <ReferenceField
        label="Harga Satuan"
        source="pricingId"
        reference="pricings"
      >
        <TextField source="price" />
      </ReferenceField>
      <TextField source="date" />
      <RichTextField source="comment" />
    </SimpleShowLayout>
  </Show>
);
