// in src/App.js
import React from "react";
import { Admin, Resource } from "react-admin";

import {
  PostList,
  PostEdit,
  PostCreate,
  PostFilter,
  PostShow
} from "./menu/ondemand";
import {
  CustomerList,
  CustomerCreate,
  CustEdit,
  CustFilter
} from "./menu/customer";
import { transactionlist } from "./menu/transaction";
import { HistoriesTransaction, HistFilter } from "./menu/historytransaction";
import { MyAccount } from "./menu/account";
import { PriceList, PriceCreate, PriceFilter, PriceEdit } from "./menu/pricing";

import Dashboard from "./Dashboard";
import authProvider from "./authProvider";
import jsonServerProvider from "ra-data-json-server";
//import simpleRestProvider from "ra-data-json-server";
//import dataProvider from './dataProvider';

import { createMuiTheme } from "@material-ui/core/styles";
import index from "@material-ui/core/colors/index";
import PostIcon from "@material-ui/icons/Book";
import UserIcon from "@material-ui/icons/Group";
import AddShoppingCart from "@material-ui/icons/AddShoppingCart";
import List from "@material-ui/icons/List";
import AccountBox from "@material-ui/icons/AccountBox";
import MonetizationOn from "@material-ui/icons/MonetizationOn";
import indonesianMessages from "ra-language-indonesian";

const messages = {
  idn: indonesianMessages
};
const i18nProvider = locale => messages[locale];

const dataProvider = jsonServerProvider(
  //"https://my-json-server.typicode.com/muhidin94/jsonserver"
  "http://localhost:3000"
);
const theme = createMuiTheme({
  palette: {
    primary: index,
    secondary: {
      main: "#0087ff"
    }
  }
});
const App = () => (
  <Admin
    title
    theme={theme}
    dashboard={Dashboard}
    authProvider={authProvider}
    dataProvider={dataProvider}
    locale="idn"
    i18nProvider={i18nProvider}
  >
    <Resource
      name="ondemands"
      list={PostList}
      edit={PostEdit}
      create={PostCreate}
      icon={PostIcon}
      filter={PostFilter}
      show={PostShow}
      options={{ label: "Permintaan" }}
    />
    <Resource
      name="customers"
      list={CustomerList}
      icon={UserIcon}
      create={CustomerCreate}
      edit={CustEdit}
      filter={CustFilter}
      options={{ label: "Bank Sampah" }}
    />
    <Resource
      name="transactions"
      list={transactionlist}
      icon={AddShoppingCart}
      options={{ label: "Transaksi" }}
    />
    <Resource
      name="histories"
      list={HistoriesTransaction}
      icon={List}
      filter={HistFilter}
      options={{ label: "Historis" }}
    />
    <Resource
      name="pricings"
      list={PriceList}
      filter={PriceFilter}
      create={PriceCreate}
      icon={MonetizationOn}
      edit={PriceEdit}
      options={{ label: "Harga" }}
    />
    <Resource
      name="account"
      list={MyAccount}
      icon={AccountBox}
      options={{ label: "Akun" }}
    />
  </Admin>
);

export default App;
