import React from "react";
import ResponsiveContainer from "recharts/lib/component/ResponsiveContainer";
import LineChart from "recharts/lib/chart/LineChart";
import Line from "recharts/lib/cartesian/Line";
import XAxis from "recharts/lib/cartesian/XAxis";
import YAxis from "recharts/lib/cartesian/YAxis";
import CartesianGrid from "recharts/lib/cartesian/CartesianGrid";
import Tooltip from "recharts/lib/component/Tooltip";
import Legend from "recharts/lib/component/Legend";

const data = [
  { name: "Mon", Jakarta: 44000, Bekasi: 28000, Bogor: 25000 },
  { name: "Tue", Jakarta: 12800, Bekasi: 23980, Bogor: 21000 },
  { name: "Wed", Jakarta: 55000, Bekasi: 43000, Bogor: 38000 },
  { name: "Thu", Jakarta: 43800, Bekasi: 29080, Bogor: 15000 },
  { name: "Fri", Jakarta: 58900, Bekasi: 28000, Bogor: 11000 },
  { name: "Sat", Jakarta: 53900, Bekasi: 38000, Bogor: 28000 },
  { name: "Sun", Jakarta: 48900, Bekasi: 41000, Bogor: 38000 }
];

function SimpleLineChart() {
  return (
    // 99% per https://github.com/recharts/recharts/issues/172
    <ResponsiveContainer width="99%" height={200}>
      <LineChart data={data}>
        <XAxis dataKey="name" />
        <YAxis />
        <CartesianGrid vertical={false} strokeDasharray="3 3" />
        <Tooltip />
        <Legend />
        <Line type="monotone" dataKey="Jakarta" stroke="#82ca9d" />
        <Line type="monotone" dataKey="Bekasi" stroke="#8884d8" />
        <Line type="monotone" dataKey="Bogor" stroke="#eb8473  " />
      </LineChart>
    </ResponsiveContainer>
  );
}

export default SimpleLineChart;
