import React from "react";
import {
  BarChart,
  Bar,
  ResponsiveContainer,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";

const data = [
  { name: "1", Jakarta: 24, Bekasi: 12, Bogor: 15 },
  { name: "2", Jakarta: 78, Bekasi: 56, Bogor: 61 },
  { name: "3", Jakarta: 120, Bekasi: 97, Bogor: 112 },
  { name: "4", Jakarta: 180, Bekasi: 147, Bogor: 156 }
];

function barChart() {
  return (
    <ResponsiveContainer width="99%" height={200}>
      <BarChart data={data}>
        <XAxis dataKey="name" stroke="#8884d8" />
        <YAxis />
        <Tooltip wrapperStyle={{ width: 100, backgroundColor: "#ccc" }} />
        <Legend
        // width={100}
        // wrapperStyle={{
        //   top: 95,
        //   right: 20,
        //   backgroundColor: "#f5f5f5",
        //   border: "1px solid #d5d5d5",
        //   borderRadius: 3,
        //   lineHeight: "20px"
        // }}
        />
        <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
        <Bar type="monotone" dataKey="Jakarta" fill="#82ca9d" barSize={20} />
        <Bar type="monotone" dataKey="Bekasi" fill="#8884d8" barSize={20} />
        <Bar type="monotone" dataKey="Bogor" fill="#eb8473  " barSize={20} />
      </BarChart>
    </ResponsiveContainer>
  );
}

export default barChart;
