// in src/Dashboard.js
import React from "react";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import { Title } from "react-admin";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import SimpleLineChart from "./components/SimpleLineChart";
import BarChart from "./components/BarChart";
import RandomChart from "./components/RandomChart";
import DashLineChart from "./components/DashLineChart";
import PositifNeg from "./components/PositifNegChart";
import AreaChartFillByValue from "./components/AreaChart";
import SimpPieChart from "./components/PieChart";
import MyComponent from "./components/test";
import Donut from "./components/GaughChart";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

function CenteredGrid(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Grid container spacing={24}>
        <Grid item xs={12}>
          <Card>
            <Title title="Dashboard" />
            <CardHeader
              style={{ textAlign: "center" }}
              title="Selamat Datang Di Tampah Buyer"
            />
          </Card>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Keseluruhan Sampah (/kg) <SimpleLineChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Bank Sampah Keseluruhan (per-kuartal) <BarChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Sampah Keseluruhan (per-bulan, /ton) <RandomChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Sampah Keseluruhan (per-bulan, /ton) <Donut />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Fluktuasi Bank Sampah <DashLineChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Fluktuasi Sampah Nasabah <PositifNeg />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Akumulasi Sampah Regional A<AreaChartFillByValue />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Akumulasi Sampah Regional B<MyComponent />
          </Paper>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper className={classes.paper}>
            Rating Sampah Regional A<SimpPieChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper className={classes.paper}>
            Rating Sampah Regional B<SimpPieChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper className={classes.paper}>
            Rating Sampah Regional C<SimpPieChart />
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
