class MyComponent extends React.Component {
  render() {
    return (
      <ResponsiveContainer width="99%" height={250}>
        <ReactFusioncharts
          type="mscombi3d"
          dataFormat="JSON"
          dataSource={dataSource}
        />
      </ResponsiveContainer>
    );
  }
}

export default MyComponent;
